@charset "UTF-8";
.event_wrap {
  /* 비주얼 - 이미지 움직이는 영역 */
  /* 서비스 노출 영역 */
  /* 반응형 디테일 작업 */
}
.event_wrap .service {
  padding: 0 15px;
}
@media screen and (min-width: 992px) {
  .event_wrap .service {
    padding: 0;
    max-width: 992px;
    margin: 0 auto;
  }
}
.event_wrap .bg01 {
  background: url("../img/slide_bg.png") center center/2240px 600px;
  width: 100%;
  height: 600px;
  animation: slidehomeBig 100s linear infinite;
}
.event_wrap .visual_moving_image_wrap {
  position: relative;
  max-width: 992px;
  margin: 0 auto;
}
.event_wrap .visual_moving_image_title {
  position: absolute;
  top: 100px;
  left: 15px;
  width: calc(100% - 30px);
  font-weight: bold;
  line-height: 1.5;
}
@media screen and (min-width: 992px) {
  .event_wrap .visual_moving_image_title {
    left: 0;
    width: 100%;
  }
}
.event_wrap .visual_moving_image_line {
  overflow-y: hidden;
  color: #fff;
  font-size: 0;
}
.event_wrap .visual_moving_image_line:last-child span:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 768px) {
  .event_wrap .visual_moving_image_line {
    display: block;
  }
}
.event_wrap .visual_moving_image_line span {
  display: inline-block;
  padding: 0 5px;
  background-color: #6eb327;
  margin-bottom: 5px;
  font-size: 28px;
  word-break: break-all;
}
@media screen and (min-width: 768px) {
  .event_wrap .visual_moving_image_line span {
    display: inline-block;
    padding: 0 10px;
    margin-bottom: 0;
    font-size: 40px;
  }
}
.event_wrap .visual_moving_image_line span.animate {
  position: relative;
  animation: text-up 1s cubic-bezier(0.4, 0, 0.2, 1) both;
}
.visual_moving_image_line:nth-of-type(2) .event_wrap .visual_moving_image_line span.animate {
  animation-delay: 0.1s;
}

.event_wrap .bg03 {
  margin: 100px 0 50px;
}
.event_wrap .showcase_detail_desc {
  font-size: 24px;
  display: block;
  font-weight: bold;
  line-height: 1.5;
  word-break: keep-all;
}
@media screen and (min-width: 992px) {
  .event_wrap .showcase_detail_desc {
    width: 60%;
    font-size: 36px;
  }
}
.event_wrap .showcase_detail_img {
  display: block;
  width: 100%;
  height: auto;
  transition: all 0.5s ease-in-out 0.1s;
  z-index: 1;
}
.event_wrap .showcase_detail_img_wrap {
  overflow: hidden;
  transition: all 0.4s ease-in-out 0s;
  transform: scale(1);
  transform-origin: center center;
  position: relative;
  background-color: #fafafa;
  min-height: 26vw;
}
.event_wrap .showcase_detail_list::after {
  display: block;
  clear: both;
  content: "";
}
.event_wrap .showcase_detail_item {
  width: 100%;
  float: left;
  margin-bottom: 40px;
}
@media screen and (min-width: 768px) {
  .event_wrap .showcase_detail_item {
    width: 50%;
  }
  .event_wrap .showcase_detail_item:nth-child(odd) {
    clear: both;
  }
  .event_wrap .showcase_detail_item:nth-child(4n+1) {
    margin-top: 80px;
  }
  .event_wrap .showcase_detail_item:nth-child(4n+2) .showcase_detail_link {
    float: right;
  }
  .event_wrap .showcase_detail_item:nth-child(4n+3) {
    margin-top: 100px;
  }
}
.event_wrap .showcase_detail_item.animate .showcase_detail_title {
  animation: fadein 1s;
  animation-fill-mode: forwards;
  animation-delay: 1s;
}
.event_wrap .showcase_detail_item.animate .showcase_detail_text {
  animation: fadein 1s;
  animation-fill-mode: forwards;
  animation-delay: 1.2s;
}
.event_wrap .showcase_detail_item.animate .showcase_detail_img_wrap {
  opacity: 1;
}
.event_wrap .showcase_detail_item.animate .showcase_detail_overlay {
  opacity: 0;
  transform: skew(0, 60deg) scale(2) translate(0, -150%);
}
.event_wrap .showcase_detail_link {
  width: 100%;
  display: inline-block;
}
@media screen and (min-width: 768px) {
  .event_wrap .showcase_detail_link {
    width: 30vw;
  }
}
@media screen and (min-width: 1200px) {
  .event_wrap .showcase_detail_link {
    width: 20vw;
  }
}
.event_wrap .showcase_detail_link:hover .showcase_detail_img_wrap {
  transform: scale(0.95);
  transform-origin: center center;
}
.event_wrap .showcase_detail_link:hover .showcase_detail_img {
  transform: scale(1.15);
  transform-origin: center center;
}
.event_wrap .showcase_detail_title {
  color: #000;
  font-size: 24px;
  margin-top: 15px;
  opacity: 0;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 36px;
  max-height: 72px;
}
.event_wrap .showcase_detail_text {
  text-align: left;
  color: #000;
  font-size: 14px;
  opacity: 0;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  line-height: 21px;
  max-height: 42px;
}
.event_wrap .showcase_detail_img_wrap {
  opacity: 0;
}
.event_wrap .showcase_detail_overlay {
  background: #fff;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 1;
  transform: skew(0, 0) scale(1) translate(0, 0);
  transition: all 2.5s ease-out;
}
.event_wrap .visual_text {
  font-size: 18px;
  line-height: 1.5;
}
@media screen and (min-width: 992px) {
  .event_wrap .visual_text {
    width: 80%;
    font-size: 24px;
  }
}
.event_wrap .visual_text:not(:last-child) {
  margin-bottom: 20px;
}
.event_wrap .visual_text_wrap {
  margin: 50px 0;
}
@media screen and (min-width: 992px) {
  .event_wrap .visual_text_wrap {
    margin: 100px 0;
  }
}
.event_wrap .point {
  font-size: 60px;
  color: #6eb327;
}
.event_wrap .event_contents.event_pc {
  padding: 0 15px;
}
.event_wrap .event_contents.event_pc .bg01 {
  width: calc(100% + 30px);
  margin-left: -15px;
  margin-right: -15px;
}
.event_wrap .event_contents.event_pc .visual_moving_image_title {
  left: 15px;
}
@media screen and (min-width: 992px) {
  .event_wrap .event_contents.event_pc {
    padding: 0;
  }
  .event_wrap .event_contents.event_pc .bg01 {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
}
.event_wrap .event_contents.event_mw {
  width: 100%;
  padding: 0 6vw;
  box-sizing: border-box;
}
.event_wrap .event_contents.event_mw .bg01 {
  width: calc(100% + 12vw);
  margin-left: -6vw;
  margin-right: -6vw;
}
.event_wrap .event_contents.event_mw .visual_moving_image_title {
  padding: 0 6vw;
}
@media screen and (min-width: 768px) {
  .event_wrap .event_contents.event_mw .showcase_detail_link {
    width: 35vw;
  }
}

@keyframes text-up {
  from {
    opacity: 0;
    transform: translate3d(0, 150%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slidehomeBig {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -2240px 0;
  }
}